import { BowlingComponent } from './bowling/bowling.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { EatComponent } from './eat/eat.component';
import { PartiesComponent } from './parties/parties.component';
import { LeaguesComponent } from './leagues/leagues.component';
import { AboutusComponent } from './aboutus/aboutus.component';
import { YouthComponent } from './youth/youth.component';
import { SpecialsComponent } from './specials/specials.component';
import { SignupComponent } from './signup/signup.component';
import { HomeComponent } from './home/home.component';


const routes: Routes = [
  {path:'bowling',component: BowlingComponent},
  {path:'eat',component: EatComponent},
  {path:'parties',component: PartiesComponent},
  {path:'leagues',component: LeaguesComponent},
  {path:'specials',component: SpecialsComponent},
  {path:'youth',component: YouthComponent},
  {path:'aboutus',component: AboutusComponent},
  {path:'sign-up',component: SignupComponent},
  {path:'home',component: HomeComponent}
  
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
export const routingComponents = [BowlingComponent,EatComponent,PartiesComponent
  ,LeaguesComponent,SpecialsComponent,YouthComponent,AboutusComponent,SignupComponent,HomeComponent]
